import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import StoreShelf from '../container/shelf/Index';
import ShoppingBasket from '../container/basket/Index';
import purple from 'material-ui/colors/purple';
import green from 'material-ui/colors/green';
import Dialog, { DialogActions, DialogContent } from 'material-ui/Dialog';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';

const styles: StyleRules<'price' |  'top' | 'shelf' | 'basket' > = {
    top: {
        width: '100%',
        display: 'flex',
        alignItems: 'stretch',
        alignContent: 'stretch'
    },
    price: {
        marginRight: 30
    },
    shelf: {
        width: '60%',
        backgroundColor: purple[800],
        overflowY: 'scroll'
    },
    basket: {
        width: '40%',
        position: 'fixed',
        right: 0,
        backgroundColor: green[800]
    }
};

type ClassNames = keyof typeof styles;

interface CatalogComponentProps {
}

interface ComponentState {
    height: number;
    open: boolean;
}

class CatalogComponent extends React.Component<CatalogComponentProps & WithStyles<ClassNames>, ComponentState> {

    private resizeHandler: ( ) => void;

    constructor(props: any) {
        super(props);
        this.state = {
            height: window.innerHeight,
            open: true
        };
        this.resizeHandler = () => {
            this.setState({
               height: window.innerHeight 
            });
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
        this.setState({open: true});
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    handelClose = () => {
        this.setState({open: false});
    }

    render() {
        const {classes} = this.props;
        return (
        <div className={classes.top} >
            <div className={classes.shelf} style={{height: this.state.height}}>
                <StoreShelf  />
            </div>
            <div className={classes.basket} style={{height: this.state.height}}>
                <ShoppingBasket />
            </div>
            <Dialog
                open={this.state.open}
                onClose={this.handelClose}
            >
            <DialogContent>
                <Typography> 
                    いらっしゃいませ <br />
                    しょうひん　を　<br />
                    おえらび ください。
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button 
                    color="primary"
                    onClick={this.handelClose}
                >
                    〇
                </Button>
            </DialogActions>
            </Dialog>
        </div>
        );
    }
}

export default withStyles<{} & ClassNames>(styles)<CatalogComponentProps>(CatalogComponent);


