import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import DialogTitle from 'material-ui/Dialog/DialogTitle';
import Dialog from 'material-ui/Dialog/Dialog';
import Slide from 'material-ui/transitions/Slide';
import DialogActions from 'material-ui/Dialog/DialogActions';
import Button from 'material-ui/Button/Button';
import { DialogState } from './models';
import DialogContent from 'material-ui/Dialog/DialogContent';
import DialogContentText from 'material-ui/Dialog/DialogContentText';

const styles: StyleRules = {

};

type ClassNames = keyof typeof styles;

function Transition(props: any) {
  return <Slide direction="up" {...props} />;
}

class Dialogs extends React.Component<Idispatcher &
                                            DialogState &
                                            WithStyles<ClassNames>, 
                                            {}> {

    closeDialog = () => {
      const {thanksDialog} = this.props;
      if (thanksDialog.closeHandle) {
        thanksDialog.closeHandle();
      }
      this.props.closeDialog();
    }

    render() {
        const {thanksDialog} = this.props;
        return (
        <Dialog
          open={thanksDialog.open}
          transition={Transition}
          keepMounted={true}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle >
            ありがとうございました！
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              おつりは{thanksDialog.change}えんです
            </DialogContentText>
          </DialogContent>
          <DialogActions onClick={this.closeDialog}>
            <Button color="primary">
              〇
            </Button>
          </DialogActions>
        </Dialog>);
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            DialogState>(Dialogs);

export default StyledLiveContainer;



