import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import green from 'material-ui/colors/green';
import Paper from 'material-ui/Paper';
import Button from 'material-ui/Button';
import AddPhoto from 'material-ui-icons/AddAPhoto';
import NoDisturb from 'material-ui-icons/DoNotDisturb';
import loadImage from '../../util/loadImg';

const styles: StyleRules<'img' | 'bottom' | 'right'| 'paper' | 'top' | 'left' | 'photo'> = {
  paper: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '80%',
    height: Math.floor(window.innerHeight * 4 / 5)
  },
  left: {
    width: '40%',
    height: '80%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
  },
  right: {
    width: '60%',
    height: '80%',
    display: 'flex',
    justifyContent: 'flex-start',
    flexDirection: 'column',
    boxSizing: 'border-box',
    padding: 30
  },
  bottom: {
    width: '100%',
    boxSizing: 'border-box',
    padding: 10
  },
  photo: {
    width: '80%',
    height: '60%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    border: 'solid 2px #888888'
  },
  img: {
    width: '100%',
    height: '100%'
  },
  top: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: green[700],
    width: '100%',
    minHeight: window.innerHeight
  }
};

type ClassNames = keyof typeof styles;

interface BaseGoodsProps {
    handleSetImg: (img: string) => void;
    img: string|undefined;
    editorActions: JSX.Element;
    editItems: JSX.Element;
}

class AddGoods extends React.Component<BaseGoodsProps & 
                                       WithStyles<ClassNames> 
                                       , {}> {

    loadImg = async () => {
      try {
        const result = await loadImage();
        this.props.handleSetImg(result);
      } catch {
        console.log('fail');
      }
    }
    
    render() {
        const {classes, img} = this.props;
        return (
          <div className={classes.top}>
                  <Paper className={classes.paper}>
                    <div className={classes.left}>
                      <div className={classes.photo}>
                        {
                          ( () => {
                            if (img !== '') {
                              return (<img src={img} className={classes.img}/>);
                            } else {
                              return (<NoDisturb color="disabled"/>);
                            }
                          } )()
                        }
                        
                      </div>
                      <Button color="primary" onClick={this.loadImg}>
                        <AddPhoto />
                        しゃしん 
                      </Button>
                    </div>
                    <div className={classes.right}>
                        {this.props.editItems}
                    </div>
                    <div className={classes.bottom}>
                        {this.props.editorActions}
                    </div>
                  </Paper>
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<
                                       BaseGoodsProps 
                                       >(AddGoods);

export default StyledLiveContainer;


