import { IrootState } from '../../reducers/Root';
import { Dispatch } from 'react-redux';
import { connect } from 'react-redux';
import Chat from './Chat';
import { newCustomer } from '../customer/actions';
import CustomerStore  from '../customer/models';
import GoodsStore, { Goods }  from '../goods/model';
import { GoodsCounter } from '../shelf/models';
import { toggleChatDialog, changeMessageState } from './actions';
import { MessageState } from './models';

export interface Idispatcher {
    newCustomer(): void;
    toggleChatDialog(open: boolean): void;
    changeMessageState(st: MessageState): void;
}

const rollGoods = (goodsList: Goods[], requestGoodsList: GoodsCounter[]): Goods => {
    const index = Math.floor(Math.random() * goodsList.length);
    const targetGoods = goodsList[index];
    const exist = requestGoodsList.find((g) => (g.id === targetGoods.id));
    if (exist) {
        return rollGoods(goodsList, requestGoodsList);
    } else {
        return targetGoods;
    }
};

class ActionDispatcher  {

    private dispatch: Dispatch<IrootState>;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    createDispatcher(): Idispatcher {
        const self = this;
        return {
            toggleChatDialog: (open: boolean) => {
                this.dispatch(toggleChatDialog(open));
            },
            changeMessageState: (st: MessageState) => {
                this.dispatch(changeMessageState(st));
            },
            newCustomer: async () => {
                const customerList = await CustomerStore.all();
                const goodsList = await GoodsStore.all();
                const cIndex = Math.floor(Math.random() * (customerList.length));
                let reqGoodsCount = Math.floor(Math.random() * 4) + 1;
                const c = customerList[cIndex];
                const requestGoodsList: GoodsCounter[] = [];
                while (reqGoodsCount) {
                    reqGoodsCount--;
                    const goods = rollGoods(goodsList, requestGoodsList);
                    const count = Math.floor(Math.random() * 3) + 1;
                    requestGoodsList.push({
                        id: goods.id,
                        displayName: goods.displayName,
                        price: goods.price,
                        img: goods.img,
                        count
                    });
                }
                self.dispatch(newCustomer(c, requestGoodsList));
            },
        };
    }
}

const dispatcher = new ActionDispatcher();
export default connect((st: IrootState) => {
                            return {...st.Customer, ...st.Shelf, ...st.Chat};
                        },
                       (dispatch: Dispatch<IrootState>) => { 
                        dispatcher.DispatchFunc = dispatch;
                        return dispatcher.createDispatcher() as Idispatcher; })
                        (Chat);


