import RUtil from './RUtil';
import { Ischeme } from './RStoreScheme';
import { DbError } from './RError';
import { RObjectStore } from './RObjectStore';

export default class Renga {

    private dbName: string;
    private indexedDb: IDBFactory; 

    constructor(dbName: string, indexedDb: IDBFactory = indexedDB) {
        this.dbName = dbName;
        this.indexedDb = indexedDb;
    }

    /**
     * 
     * @param storeName 
     * @param keyName 
     * @param storeIndexList 
     */
    async createObjectStoreIfNotExist(scheme: any) {
        const ischeme = <Ischeme> scheme;
        if (!ischeme.keyString) {
            throw new DbError('@key decorator has not been set');
        }
        if (!ischeme.storeName) {
            throw new DbError('@scheme class decorator has not been set');
        }
        const storeName = ischeme.storeName;
        const keyString = ischeme.keyString;
        const exist = await RUtil.existObjectStore(this.indexedDb, this.dbName, storeName);
        if (!exist) {
            const version = await RUtil.dbVersion(this.indexedDb, this.dbName);
            await new Promise<boolean>((resolve) => {
                const openReq = indexedDB.open(this.dbName, version + 1);
                openReq.onupgradeneeded = (event: IDBVersionChangeEvent) => {
                    const db = (<IDBRequest> event.target).result;
                    const store = db.createObjectStore(storeName, {keyPath : keyString});
                    for (let indexInfo of ischeme.indexList) {
                        store.createIndex(indexInfo.Name, indexInfo.Name, { unique : indexInfo.Unique });
                    }
                    db.close();
                    resolve(true);
                };
            });
        }
        const created = !exist;
        return created;
    }

    objectStore<T>(so: any): RObjectStore<T> {
        const instance = new so();
        return new RObjectStore<T>(this.indexedDb, this.dbName, (<Ischeme> instance).storeName);
    }
}


