
class IndexInfo {
    private name: string;
    private unique: boolean;

    constructor(name: string, unique: boolean) {
        this.name = name;
        this.unique = unique;
    }

    get Name(): string {
        return this.name;
    }

    get Unique(): boolean {
        return this.unique;
    }
}

export interface Ischeme {
    storeName: string;
    keyString: string;
    indexList: IndexInfo[];
}

export abstract class SchemeObject {

}

 export function scheme (className: string) {
    return <Clazz extends Function> (clazz: Clazz ) => {
        clazz.prototype.storeName = className;
    };
}

/**
 * key decorator
 * @param target 
 * @param name 
 */
export function key(target: any, name: string) {
    Object.defineProperty(target, 'keyString', {value: name, 
                                                writable: true});
}

/**
 * index decorator
 * @param target 
 * @param name 
 */
export function index(target: any, name: string) {
    generateIndexInfo(target, name);
}

/**
 * unique index decorator
 * @param target 
 * @param name 
 */
export function unique_index(target: any, name: string) {
    generateIndexInfo(target, name, true);
}

function generateIndexInfo(target: any, name: string, unique: boolean = false) {
    const descriptor = Object.getOwnPropertyDescriptor(target, 'indexList');
    if (descriptor) { 
        descriptor.value.push(new IndexInfo(name, unique));
    } else {
        Object.defineProperty(target, 'indexList', {value: [new IndexInfo(name, unique)], 
                                                        writable: true});
    }
}


