export enum AmountType {
    Yen1 = 1,
    Yen5 = 5,
    Yen10 = 10,
    Yen50 = 50,
    Yen100 = 100,
    Yen500 = 500,
    Yen1000 = 1000,
    Yen5000 = 5000,
    Yen10000 = 10000,
}

export class ReceiptStrings {
   strList: string[];
}

interface Amount {
    displayName: string;
    value: number;
    type: AmountType;
    img: string;
}

export interface CoinCounter {
    type: AmountType;
    count: number;
}

