import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import PaymentComponent from './components/PaymentComponent';
import { Switch, Route } from 'react-router';
import ShoppingComponent from './components/ShoppingComponent';
import Dialogs from './container/dialogs/Index';
import EditGoodsTable from './container/editTable/goods/Index';
import EditCustomerTable from './container/editTable/customer/Index';
import AddGoods from './container/add/goods/Index';
import AddCustomer from './container/add/customer/Index';
import ReactToChat from './components/ReactChatComponent';
import License from './components/ShowLicense';
import dc from './constants/DomainConstants';

const styles: StyleRules<'root' > = {
  root: {
    top: 0,
    margin: 0,
    height: '100%',
    width: '100%',
    color: '#FFFFFF'
  },
};

type ClassNames = keyof typeof styles;

interface AppProps {
}

interface IndexState {
  state: number;
}

class App extends React.Component<AppProps & WithStyles<ClassNames>, IndexState> {

  constructor(props: any) {
    super(props);
    this.state = {
      state: 0
    } as IndexState;
  }

  componentDidMount() {
    window.addEventListener('resize', () => {
      this.setState({state: this.state.state + 1});
    });
  }

  render() {
    const {classes} = this.props;
    return (
      <div className={classes.root}>
        <Switch>
          <Route exact={true} path={dc.home} component={ReactToChat}/>
          <Route path={dc.shopping} component={ShoppingComponent}/>
          <Route path={dc.pay} component={PaymentComponent}/>
          <Route path={dc.goods} component={EditGoodsTable}/>
          <Route path={dc.customer} component={EditCustomerTable}/>
          <Route path={dc.addGoods} component={AddGoods}/>
          <Route path={dc.addCustomer} component={AddCustomer}/>
          <Route path={dc.license} component={License}/>
        </Switch>
        <Dialogs />
      </div>
    );
  }
}

export default withStyles<{}&ClassNames>(styles)<AppProps>(App);



