import { StyleRules } from 'material-ui/styles/withStyles';
import green from 'material-ui/colors/green';
import grey from 'material-ui/colors/grey';

export const styles: StyleRules<'addBut' | 'avaterBut' 
                         | 'avaterAction' | 'table' | 'navi' | 'top' | 'inputValue' | 'avaterCell'> = {
  navi: {
    width: '100%',
    display: 'flex',
    justifyContent: 'space-between',
  },
  addBut: {
    marginRight: 40,
    display: 'flex',
    flexDirection: 'column'
  },
  avaterCell: {
    position: 'relative',
  },
  avaterBut: {
    borderRadius: 50
  },
  avaterAction: {
    position: 'absolute',
    bottom: 0
  },
  top: {
    backgroundColor: green[700],
    color: grey[10],
    minHeight: window.innerHeight
  },
  table: {
    paddingTop: 57,
    paddingBottom: 70
  },
  inputValue: {
    color: '#000000',
    paddingLeft: 5,
    backgroundColor: '#F3F3F3'
  }
};

export type ClassNames = keyof typeof styles;

export const colStyle = {
  color: grey[300]
}; 

export const cellStyle = {
  color: grey[300]
};


