import { DbError } from './RError';

export default class RUtil {
    /**
     * get indexed db version
     */
    static async dbVersion(indexedDb: any, dbName: string): Promise<number> {
        const version = await new Promise<number>(
                (resolve, reject) => {
                    const openReq  = indexedDb.open(dbName);
                    openReq.onsuccess = (event: IDBVersionChangeEvent) => {
                        const db = (<IDBRequest> event.target).result;
                        const ver = db.version;
                        db.close();
                        if (ver instanceof Error) {
                            resolve(0);
                        }
                        resolve(ver);
                    };
                    openReq.onerror = (event: any) => {
                        event.target.result.close();
                        reject(-1);
                    };
                }
            );
        if (version === -1) {
            throw new DbError('get version fail');
        }
        return version;
    }

    /**
     * does store object exists
     * if exists return true, 
     * else false 
     * @param dbName 
     * @param storeName 
     */
    static async existObjectStore(indexedDb: any, dbName: string, storeName: string): Promise<boolean> {
        return new Promise<boolean>((resolve, reject) => {
            var openReq = indexedDB.open(dbName);
            openReq.onsuccess = (event: IDBVersionChangeEvent) => {
                var db = (<IDBRequest> event.target).result;
                if (db.objectStoreNames.contains(storeName)) {
                    db.close();
                    resolve(true);
                } else {
                    db.close();
                    resolve(false);
                }
            };
        });
    }
}


