import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { ShelfState } from './models';
import GridList from 'material-ui/GridList/GridList';
import GridListTile from 'material-ui/GridList/GridListTile';
import GridListTileBar from 'material-ui/GridList/GridListTileBar';
import { Goods } from '../goods/model';

const styles: StyleRules<  'top'  > = {
    top: {
        marginTop: 1,
        width: '100%',
    },
};

type ClassNames = keyof typeof styles;

interface StoreShelfProps {
}

class StoreShelfContainer extends React.Component<Idispatcher &
                                            StoreShelfProps &
                                            ShelfState &
                                            WithStyles<ClassNames>, 
                                            {}> {

    selectGoods = (goods: Goods) => {
        this.props.select(goods);
    }

    render(): JSX.Element {
        const { classes, GoodsList }  = this.props;
        return (
        <div className={classes.top}>
            <GridList cellHeight={180} cols={3} >
            {GoodsList.map((tile) => {
                return (
                    <GridListTile key={tile.id} onClick={this.selectGoods.bind(this, tile)}>
                        <img src={tile.img} alt={tile.displayName} />
                        <GridListTileBar
                            titlePosition="bottom"
                            title={tile.displayName}
                            subtitle={<span>{tile.price}えん</span>}
                        />
                    </GridListTile>
                );
                })
            }
            </GridList>
        </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            StoreShelfProps & ShelfState>(StoreShelfContainer);

export default StyledLiveContainer;


