import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { GoodsCounter } from '../../shelf/models';
import { DialogActions } from 'material-ui/Dialog';
import Typography from 'material-ui/Typography';
import Avater from 'material-ui/Avatar';
import CancelIcon from 'material-ui-icons/Cancel';
import DoneIcon from 'material-ui-icons/Done';
import Button from 'material-ui/Button';

const styles: StyleRules<'requestGoods'|'root'|'content'|'bought'|'request'> = {
    root: {
        width: '100%',
    },
    content: {
        display: 'flex'
    },
    bought: {
        borderRadius: 10,
        border: '2px solid #FFFFFF',
        display: 'flex',
        width: '50%',
        flexWrap: 'wrap',
    },
    request: {
        borderRadius: 10,
        border: '2px solid #FFFFFF',
        display: 'flex',
        width: '50%',
        flexWrap: 'wrap',
    },
    requestGoods: {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
    }
};

type ClassNames = keyof typeof styles;

interface RequestProps {
    requestCounterList: GoodsCounter[];
    chatContentCss: string;
}

export const Request = withStyles(styles)<RequestProps>(
    (props: RequestProps & WithStyles<ClassNames>) => {
        const {classes, chatContentCss, requestCounterList} = props;
        return (
                <div className={chatContentCss}>
    <Typography>
    おねがい が あるんだけど、、、
    </Typography>
    {requestCounterList.map((rg) => {
        return (
        <div className={classes.requestGoods} key={rg.id}>
            <Avater src={rg.img}/> 「{rg.displayName}」 を {rg.count}こ
        </div>
        );
    })}
    <Typography>
    かってきて くれない？
    </Typography>
    </div>);
    }
);

interface RequestActionProps {
    handleClickCancel: () => void;
    handleClickOk: () => void;
}

export const RequestAction = withStyles(styles)<RequestActionProps>(
    (props: RequestActionProps & WithStyles<ClassNames>) => {
        return (
                    <DialogActions>
                            <Button onClick={props.handleClickCancel}>
                                <CancelIcon color="error"/>
                                いやだ
                            </Button>
                            <Button onClick={props.handleClickOk}>
                                <DoneIcon color="secondary" />
                                いいよ！
                            </Button>
                    </DialogActions>

        );
    }
);


