import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { Idispatcher } from './Index';
import Table, { TableBody, TableCell, TableHead, TableRow } from 'material-ui/Table';
import { RouteComponentProps, withRouter } from 'react-router';
import Avatar from 'material-ui/Avatar';
import IconButton from 'material-ui/IconButton';
import BackIcon from 'material-ui-icons/ArrowBack';
import AddIcon from 'material-ui-icons/Add';
import Input  from 'material-ui/Input';
import DeleteIcon from 'material-ui-icons/Delete';
import AddPhotoIcon from 'material-ui-icons/AddAPhoto';
import Typography from 'material-ui/Typography';
import dc from '../../../constants/DomainConstants';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import Button from 'material-ui/Button';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import loadImage from '../../../util/loadImg';
import ButtonBase from 'material-ui/ButtonBase';
import CustomerStore, { Customer } from '../../customer/models';
import { ClassNames, styles, cellStyle, colStyle } from '../styles/styles';

class GoodsEditTable extends React.Component<Idispatcher &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>,
                                            {
                                             openDeleteDialog: boolean,
                                             selectedCustomer: Customer|null
                                             customerList: Customer[]
                                            }> {
    constructor(props: any) {
      super(props);
      this.state = {
        openDeleteDialog: false,
        selectedCustomer: null,
        customerList: []
      };
    }

    fetchCustomerData() {
      CustomerStore.all().then((customerList) => {
        this.setState({
          customerList
        });
      });
    }

    async componentDidMount() {
       this.fetchCustomerData();
    }

    handleBack = () => {
      this.props.history.goBack();
    }

    linkToAddCustomer = () => {
      this.props.history.push(dc.addCustomer);
    }

    openDeleteDialog = (customer: Customer) => {
      this.setState({
        selectedCustomer: customer,
        openDeleteDialog: true
      });
    }

    doDelete = async () => {
      if (this.state.selectedCustomer) {
        await this.props.deleteCustomer(this.state.selectedCustomer);
      }
      this.setState({
        selectedCustomer: null,
        openDeleteDialog: false
      });
      this.fetchCustomerData();
    }

    cancelDelete = () => {
      this.setState({
        selectedCustomer: null,
        openDeleteDialog: false
      });
    }

    changeName = async (targetCustomer: Customer, nameStr: string) => {
      await this.props.changeName(targetCustomer, nameStr);
      this.fetchCustomerData();
    }

    updateCustomer = async (customer: Customer) => {
      await this.props.updateCustomer(customer);
      this.fetchCustomerData();
    }

    changeImg = async (customer: Customer) => {
      const result = await loadImage();
      await this.props.changeImage(customer, result);
      this.fetchCustomerData();
    }

    render() {
        const {classes} = this.props;
        const {customerList, openDeleteDialog} = this.state;
        return (
          <div className={classes.top}>
          <AppBar position="fixed">
            <Toolbar className={classes.navi}>
              <IconButton onClick={this.handleBack}>
                <BackIcon style={cellStyle}/>
              </IconButton>
              <ButtonBase 
                className={classes.addBut}
                onClick={this.linkToAddCustomer}
              >
                <AddIcon style={cellStyle}/>
                <Typography style={cellStyle}>ついか</Typography>
              </ButtonBase>
            </Toolbar>
          </AppBar>
            <div className={classes.table}>
              <Table >
              <TableHead>
                <TableRow>
                  <TableCell>
                    <Typography style={colStyle}>
                    イメージ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                      なまえ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    さくじょ
                    </Typography>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {customerList.map(n => {
                  return (
                    <TableRow key={n.id}>
                      <TableCell className={classes.avaterCell}>
                          <Button
                            size="small" 
                            className={classes.avaterBut}
                            onClick={ () => {
                              this.changeImg(n);
                            }}
                          >
                            {(() => {
                              if (n.img === '' || n.img === undefined) {
                                return (<Avatar><AddPhotoIcon /></Avatar>);
                              } else {
                                return <Avatar src={n.img}/>;
                              }
                            })()}
                          </Button>
                       </TableCell>
                      <TableCell >
                          <Input
                            className={classes.inputValue}
                            value={n.name}
                            onChange={(evt: React.ChangeEvent<HTMLInputElement>) => {
                              this.changeName(n, evt.target.value);
                            }}
                            onBlur={async (evt: any) => {
                              await this.updateCustomer(n);
                            }}                           
                          />
                      </TableCell>
                      <TableCell>
                        <IconButton 
                          style={cellStyle} 
                          onClick={this.openDeleteDialog.bind(this, n)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
          </Table>
          </div>
          <Dialog open={openDeleteDialog}>
            <DialogTitle>ちゅうい</DialogTitle>
            <DialogContent>
              <DialogContentText>
                おきゃくさん を けします <br/>
                いいですか？
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.cancelDelete} color="default">
                ×
              </Button>
              <Button onClick={this.doDelete} color="primary">
                〇
              </Button>
            </DialogActions>
          </Dialog>
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                             RouteComponentProps<any>>(GoodsEditTable);

export default withRouter(StyledLiveContainer);



