import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import ChatIcon from 'material-ui-icons/Announcement';
import {  RouteComponentProps, withRouter } from 'react-router';
import ButtonBase from 'material-ui/ButtonBase';
import Avater from 'material-ui/Avatar';
import Dialog, { DialogTitle, DialogContent, DialogActions } from 'material-ui/Dialog';
import Slide from 'material-ui/transitions/Slide';
import grey from 'material-ui/colors/grey';
import Typography from 'material-ui/Typography';
import { CustomerState } from '../customer/models';
import { ShelfState } from '../shelf/models';
import dc from '../../constants/DomainConstants';
import CheckAnswerComponent from './component/CheckAnswaerComponent';
import { MessageState, ChatState } from './models';
import { Request, RequestAction } from './component/RequestComponent';
import Button from 'material-ui/Button';

function Transition(props: any) {
  return <Slide direction="up" {...props} />;
}

const styles: StyleRules<'chatContent' | 'dialog' | 
            'dialogContent' | 'chatStartBut' | 'top' | 'container' > = {
    top: {
        width: '100%',
        height: '100%'
    },
    container: {
        width: '100%',
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    chatStartBut: {
        borderRadius: '50%',
    },
    dialogContent: {
        display: 'flex',
        overflow: 'scroll'
    },
    dialog: {
    },
    chatContent: {
        width: '60%',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        borderRadius: 5,
        padding: 10,
        marginLeft: 20,
        backgroundColor: grey[300],
        '&:before': {
                    position: 'absolute',
                    top: -3,
                    left: -30,
                    borderRight: '20px solid ' + grey[300],
                    borderTop: '20px solid transparent',
                    borderLeft: '20px solid transparent',
                    borderBottom: '20px solid transparent',
                    content: '""'
        },
    },

};

type ClassNames = keyof typeof styles;

interface BasketProps {
}

const createThankYouDialog = (classes: Record<ClassNames, string>) => {
    return (
    <div className={classes.chatContent}>
    <Typography>
        ありがとう！<br /> 
        とっても　たすかる!
    </Typography>
    </div>);
};

const createUnfortunateDialog = (classes: Record<ClassNames, string>) => {
    return (
    <div className={classes.chatContent}>
    <Typography>
        うーん ざんねん<br /> 
        また、おねがいね
    </Typography>
    </div>);
};
const doShakeAnimation = () => {
    const rotateSettings = [
        'rotate(-10deg) translateY(-3%)',
        'rotate(-9deg) translateY(-5%)',
        'rotate(-8deg) translateY(-7%)',
        'rotate(-5deg) translateY(-9%)',
        'rotate(0deg) translateY(-15%)',
        'rotate(5deg) translateY(-9%)',
        'rotate(8deg) translateY(-7%)',
        'rotate(9deg) translateY(-5%)',
        'rotate(10deg) translateY(-3%)',
        ''
    ];
    
    const element = document.getElementById('chat_start_but');
    if (element) {
        let index = 0;
        const loop = 2;
        let currentLoop = 0;
        
        const shake = () => {
            element.style.transform = rotateSettings[index];
            index++;
            if (index === rotateSettings.length) {
                index = 0;
                currentLoop++;
            }
            if (currentLoop > loop) {
                currentLoop = 0;
                element.style.transform = '';
                setTimeout(shake, 1500);
            } else {
                requestAnimationFrame(shake);
            }
        };
        requestAnimationFrame(shake);
    }
};

class BasketContainer extends React.Component<Idispatcher &
                                            BasketProps &
                                            CustomerState &
                                            ShelfState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames> &
                                            ChatState,
                                            {}> {
    constructor(props: any) {
        super(props);
    }
    
    componentDidMount() {
        doShakeAnimation();
        if (!this.props.currentCustomer) {
            this.props.newCustomer();
        }
    }

    handleClickChatButton = () => {
        this.props.toggleChatDialog(true);
    }

    handleClickCancelButton = () => {
        if (this.props.messageState !== MessageState.request) {
            return;
        } 
        this.props.changeMessageState(MessageState.RejectRequst);
    }

    handleClickOkButton = () => {
        if (this.props.messageState !== MessageState.request) {
            return;
        } 
        this.props.changeMessageState(MessageState.AcceptRequest);
    }

    handleCloseChatDialog = () => {
        const { toggleChatDialog, messageState } = this.props;
        toggleChatDialog(false);
        switch (messageState) {
            case MessageState.AcceptRequest:
                this.props.history.push(dc.shopping);
                break;
            case MessageState.RejectRequst:
            case MessageState.CheckRequest:
                setTimeout(
                    () => {
                        this.props.changeMessageState(MessageState.request);
                        this.props.newCustomer();
                    },
                    500
                );
               break;
            default:
                break;
        }
        
    }

    render(): JSX.Element {
        const {openChatDialog, messageState,  
               classes, requestGoodsList, currentCustomer, counterList }  = this.props;
        const avaterImg = currentCustomer ? currentCustomer.img : '';
        return (
        <div className={classes.top}>
            <div className={classes.container}>
                <ButtonBase 
                    className={classes.chatStartBut}
                    id="chat_start_but"
                    onClick={this.handleClickChatButton}
                >
                    <ChatIcon style={{width: '80%', height: '80%'}}/>
                </ButtonBase>
            </div>
            <Dialog
                open={openChatDialog}
                transition={Transition}
                maxWidth={'md'}
                fullWidth={true}
                className={classes.dialog}
            >
                <DialogTitle >メッセージ</DialogTitle>
                <DialogContent className={classes.dialogContent}>
                    <div>
                        <Avater src={avaterImg}/>
                        <Typography>
                            {currentCustomer ? currentCustomer.name : ''}
                        </Typography>
                    </div>
                    {messageState === MessageState.request  &&
                    <Request 
                        requestCounterList={requestGoodsList}
                        chatContentCss={classes.chatContent}
                    />}
                    {messageState === MessageState.AcceptRequest  &&  createThankYouDialog(classes)}
                    {messageState === MessageState.RejectRequst  &&  createUnfortunateDialog(classes)}
                    {messageState === MessageState.CheckRequest  &&
                    <CheckAnswerComponent  
                                    chatContentCss={classes.chatContent}
                                    boughtCounterList={counterList}
                                    requestCounterList={requestGoodsList}
                    />}
                </DialogContent>
                {messageState === MessageState.request  &&
                    <RequestAction
                        handleClickCancel={this.handleClickCancelButton}
                        handleClickOk={this.handleClickOkButton}
                    />
                }
                {messageState !== MessageState.request  &&
                    <DialogActions>
                        <Button color="primary" onClick={this.handleCloseChatDialog}>
                            〇
                        </Button>
                    </DialogActions>
                }
            </Dialog>
        </div>
        );
    }
}

const StyledContainer = withRouter(withStyles<{} & ClassNames>(styles)<Idispatcher & 
                    ChatState & BasketProps & CustomerState & ShelfState  & RouteComponentProps<any>>(BasketContainer));

export default StyledContainer;


