import * as React from 'react';
import * as ReactDOM from 'react-dom';
import App from './App';
import { Provider } from 'react-redux';
import store from './store/Store';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { MuiThemeProvider, createMuiTheme } from 'material-ui/styles/';
import purple from 'material-ui/colors/purple';
import registerServiceWorker from './registerServiceWorker';
import './index.css';
import orange from 'material-ui/colors/orange';
import green from 'material-ui/colors/green';
import grey from 'material-ui/colors/grey';
import dc from './constants/DomainConstants';
import GoodsStore from './container/goods/model';
import CustomerStore from './container/customer/models';
import { SetGoodsList } from './container/shelf/actions';

const muiTheme = createMuiTheme({
  palette: {
    primary: orange,
    secondary: green,
    error: purple,
    divider: grey[300]
  },
});

ReactDOM.render(
  <Provider store={store}>
    <MuiThemeProvider theme={muiTheme}>
        <Router>
          <Route path={dc.home} component={App} />
        </Router>
    </MuiThemeProvider>
  </Provider>,
  document.getElementById('root') as HTMLElement
);

// initialze
GoodsStore.fetchDbData(async () => {
  const goodsList = await GoodsStore.all();
  store.dispatch(SetGoodsList(goodsList));
}).then(async () => {
  await CustomerStore.fetchDbData();
});

registerServiceWorker();



