import { key, index, scheme } from '../../renga/RStoreScheme';
import { RObjectStore } from '../../renga/RObjectStore';
import Renga from '../../renga/Renga';
import { defaultCustomers } from './defaultdata';
import { GoodsCounter } from '../shelf/models';

@scheme('Customer')
export class Customer {
    @key
    id: string;
    @index
    name: string;
    @index
    img: string;
}

export type NewCustomer = {
    customer: Customer;
    requestGoodsList: GoodsCounter[]
};

const dbName = 'learnrezi';

class CustomerStore {
    private renga: Renga;
    private customerObjectStore: RObjectStore<Customer>;

    constructor() {
        this.renga = new Renga(dbName);
    }

    async fetchDbData() {
        const created = await this.renga.createObjectStoreIfNotExist(new Customer());
        this.customerObjectStore = await this.renga.objectStore<Customer>(Customer);
        if (created) {
            for (const c of defaultCustomers) {
                await this.customerObjectStore.put(c);
            }
        }
    }

    async delete(customer: Customer) {
        await this.customerObjectStore.deleteByKey(customer.id);
    }

    async all() {
        if (!this.customerObjectStore) {
            await this.fetchDbData();
        }
        return await this.customerObjectStore.all();
    }

    async put (customer: Customer) {
       await this.customerObjectStore.put(customer);
    }
}

export default new CustomerStore();

export type CustomerState = {
    currentCustomer: Customer|undefined
    requestGoodsList: GoodsCounter[]
};



