import { handleActions, Action } from 'redux-actions';
import { CustomerState, Customer, NewCustomer } from '../customer/models';
import * as AT from '../../constants/ActionTypes';

const initialState: CustomerState = <CustomerState> {
    currentCustomer: undefined,
    requestGoodsList: []
};

export default handleActions<CustomerState,  Customer | NewCustomer>(
    {
        [AT.NEW_CUSTOMER]: (state: CustomerState, action: Action<NewCustomer>): CustomerState => {
            if (!action.payload) {
                return state;
            }
            const newCustomer = action.payload;
            const newState = Object.assign({}, state);
            newState.currentCustomer = newCustomer.customer;
            newState.requestGoodsList = newCustomer.requestGoodsList;
            return newState;
        },
    }, 
    initialState
);



