import { createAction } from 'redux-actions';
import { Goods } from '../goods/model';
import * as AT from '../../constants/ActionTypes';

export const selectGoods = createAction <Goods, Goods >(
  AT.SELECT_GOODS,
  (goods: Goods) => ( goods)
);

export const increaseGoods = createAction < string, string >(
  AT.INCREASE_GOODS,
  (id: string) => ( id)
);

export const decreaseGoods = createAction < string, string >(
  AT.DECREASE_GOODS,
  (id: string) => (id)
);



