import { createAction } from 'redux-actions';
import { ReceiptStrings, AmountType } from './models';
import * as AT from '../../constants/ActionTypes';

export const recivedMoney = createAction<ReceiptStrings, string[]>(
  AT.RECIVED_MONEY,
  (strList: string[]) => ({ strList })
);

export const incrementCoin = createAction<AmountType, AmountType>(
  AT.INCREMENT_COIN,
  (type: AmountType) => (type)
);

export const decrementCoin = createAction<AmountType, AmountType>(
  AT.DECREMENT_COIN,
  (type: AmountType) => (type)
);

export const closeRecivedMoneyDilaog = createAction(
  AT.CLOSE_RECEIVED_MONEY_DIALOG
);



