import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import MenuIcon from 'material-ui-icons/Menu';
import EditIcon from 'material-ui-icons/Edit';
import IconButton from 'material-ui/IconButton';
import Drawer from 'material-ui/Drawer';
import List from 'material-ui/List';
import ListItem from 'material-ui/List/ListItem';
import { RouteComponentProps, withRouter } from 'react-router';
import ListItemText from 'material-ui/List/ListItemText';
import ListItemIcon from 'material-ui/List';
import dc from '../../constants/DomainConstants';
import { Idispatcher } from './Index';
import { MenuState } from './models';
import Divider from 'material-ui/Divider';
import LicenceIcon from 'material-ui-icons/Copyright';

const styles: StyleRules<'root' | 'button'> = {
    root: {
    },
    button: {
    }
};

type ClassNames = keyof typeof styles;

interface MenuProps {
}

class MenuComponent extends React.Component<
                    MenuProps & 
                    RouteComponentProps<any> & 
                    WithStyles<ClassNames> &
                    MenuState &
                    Idispatcher,
                    {}> {

    closeDrawer = () => {
        this.props.toggleDrawer(false);
    }

    oepnDrawer = () => {
        this.props.toggleDrawer(true);
    }

    linkToEditGoodsTable = () => {
        this.props.history.push(dc.goods);
        this.closeDrawer();
    }

    linkToEditCustomerTable = () => {
        this.props.history.push(dc.customer);
        this.closeDrawer();
    }

    handleLicense = () => {
        this.props.history.push(dc.license);
        this.closeDrawer();
    }

    render() {
        const {classes, showMenuButton, openMenuDrawer} = this.props;
        let ButtonStyle = {};
        if (!showMenuButton) {
            ButtonStyle = { display: 'none'};
        }
        return (
        <div className={classes.root}>
            <IconButton 
                color="default" 
                className={classes.button}
                onClick={this.oepnDrawer}
                style={ButtonStyle}
            >
                <MenuIcon />
            </IconButton>
            <Drawer open={openMenuDrawer} onClose={this.closeDrawer}>
          <div
          >
            <List>
            <ListItem button={true} onClick={this.linkToEditGoodsTable}>
                <ListItemIcon>
                    <EditIcon />
                </ListItemIcon>
                <ListItemText primary="しょうひん　ついか・へんしゅう" />
            </ListItem>
            <ListItem button={true} onClick={this.linkToEditCustomerTable}>
                <ListItemIcon>
                    <EditIcon />
                </ListItemIcon>
                <ListItemText primary="おきゃくさん　ついか・へんしゅう" />
            </ListItem>
            <Divider />
            <ListItem button={true} onClick={this.handleLicense}>
                <ListItemIcon>
                    <LicenceIcon />
                </ListItemIcon>
                <ListItemText primary="license" />
            </ListItem>
            </List>
          </div>
        </Drawer>
        </div>
        );
    }
}

const styled = withStyles<{} &  ClassNames>(styles)
                        <RouteComponentProps<any> &
                         MenuProps&
                         Idispatcher &
                         MenuState>(MenuComponent);

export default withRouter(styled);


