import { createAction } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';

export const toggleMenuButton = createAction<boolean, boolean>(
  AT.TOGGLE_MENU_BUTTON,
  (show: boolean) => ( show )
);
export const toggleMenuDrawer = createAction<boolean, boolean>(
  AT.TOGGLE_MENU_DRAWER,
  (show: boolean) => ( show )
);


