import { handleActions, Action } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';
import { EditGoodsState } from './models';

const initialState: EditGoodsState = <EditGoodsState> {
    id: '',
    name: '',
    price: 0,
    img: ''
};

export default handleActions<EditGoodsState, string | number>(
    {
        [AT.SET_GOODS_NAME]: (state: EditGoodsState, action: Action<string>): EditGoodsState => {
            const newState = Object.assign({}, state);
            newState.name = action.payload;
            return newState;
        },
        [AT.SET_GOODS_PRICE]: (state: EditGoodsState, action: Action<number>): EditGoodsState => {
            const newState = Object.assign({}, state);
            newState.price = <number> action.payload;
            return newState;
        },
        [AT.SET_GOODS_IMG]: (state: EditGoodsState, action: Action<string>): EditGoodsState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.img = action.payload;
            return newState;
        },
        [AT.RESET_EDIT_GOODS_STATE]: (state: EditGoodsState): EditGoodsState => {
            return initialState;
        },
    }, 
    initialState
);


