import { createAction } from 'redux-actions';
import { ToggleThanksDialog } from './models';
import * as AT from '../../constants/ActionTypes';

export const toggleThanksDialog = createAction<ToggleThanksDialog, boolean, number, (() => void)|undefined>(
  AT.TOGGLE_THANKS_DIALOG,
  (show: boolean, change: number, closeHandle?: (() => void)|undefined) => ({ show, change, closeHandle})
);


