import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import green from 'material-ui/colors/green';
import TextField from 'material-ui/TextField';
import loadImage from '../../util/loadImg';
import EditComponent from './EditorComponent';

const styles: StyleRules<'top'> = {
  top: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: green[700],
    width: '100%',
    minHeight: window.innerHeight
  }
};

type ClassNames = keyof typeof styles;

interface BaseGoodsProps {
    handleSetImg: (img: string) => void;
    handleSetName: (name: string) => void;
    name: string|undefined;
    img: string|undefined;
    editorActions: JSX.Element;
}

class AddCustomer extends React.Component<BaseGoodsProps & 
                                       WithStyles<ClassNames> 
                                       , {}> {

    loadImg = async () => {
      try {
        const result = await loadImage();
        this.props.handleSetImg(result);
      } catch {
        console.log(fail);
      }
    }
    
    changeName = (evt: any) => {
      this.props.handleSetName(evt.target.value); 
    }

    render() {
        const {classes, name, img, handleSetImg} = this.props;
        return (
          <div className={classes.top}>
                  <EditComponent 
                    handleSetImg={handleSetImg}
                    img={img}
                    editItems={
                    <TextField
                        label="なまえ"
                        value={name}
                        onChange={this.changeName}
                    />
                    }
                    editorActions={
                      this.props.editorActions
                    }
                  />
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<
                                       BaseGoodsProps 
                                       >(AddCustomer);

export default StyledLiveContainer;



