import { createAction } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';
import { MessageState } from './models';

export const changeMessageState = createAction <MessageState, MessageState >(
  AT.CHANGE_MESSAGE_STATE,
  (msgSt: MessageState) => (msgSt)
);

export const toggleChatDialog = createAction <boolean, boolean >(
  AT.TOGGLE_CHAT_DIALOG,
  (open: boolean) => (open)
);


