import { createAction } from 'redux-actions';
import { Customer, NewCustomer } from './models';
import * as AT from '../../constants/ActionTypes';
import { GoodsCounter } from '../shelf/models';

export const newCustomer = createAction<NewCustomer, Customer, GoodsCounter[]> (
  AT.NEW_CUSTOMER,
  (customer, requestGoodsList) => ({ customer, requestGoodsList })
);



