import { combineReducers } from 'redux';
import Live from '../container/live/reducer';
import Amount from '../container/amount/reducer';
import Shelf from '../container/shelf/reducer';
import Dialogs from '../container/dialogs/reducer';
import OCR from '../container/ocr/reducer';
import EditGoods from '../container/add/reducer';
import Menu from '../container/menu/reducer';
import Customer from '../container/customer/reducer';
import Chat from '../container/chat/reducer';
import { LiveState } from '../container/live/models';
import { ShelfState } from '../container/shelf/models';
import { ReceiptAmountState } from '../container/amount/models';
import { DialogState } from '../container/dialogs/models';
import { OCRState } from '../container/ocr/models';
import { EditGoodsState } from '../container/add/models';
import { MenuState } from '../container/menu/models';
import { CustomerState } from '../container/customer/models';
import { ChatState } from '../container/chat/models';

const RootReducer = combineReducers({
    Live,
    Amount,
    Shelf,
    Dialogs,
    OCR,
    EditGoods,
    Menu,
    Customer,
    Chat
});

export interface IrootState {
    Live: LiveState;
    Amount: ReceiptAmountState;
    Shelf: ShelfState;
    Dialogs: DialogState;
    OCR: OCRState;
    EditGoods: EditGoodsState;
    Menu: MenuState;
    Customer: CustomerState;
    Chat: ChatState;
}

export default RootReducer;


