import RUtil from './RUtil';
import { SchemeObject } from './RStoreScheme';

/**
 * 
 */
export class RObjectStore<T extends SchemeObject> {
    private dbName: string;
    private storeName: string;
    private indexedDb: IDBFactory;

    constructor(indexedDb: IDBFactory, dbName: string, storeName: string) {
        this.dbName = dbName;
        this.indexedDb = indexedDB;
        this.storeName = storeName;
    }

    async all(): Promise<T[]> {
        return new Promise<T[]>((resolve) => {
            var resultList: T[] = [];
            const storeName = this.storeName;
            const openReq = this.indexedDb.open(this.dbName);
            openReq.onsuccess = (result: IDBVersionChangeEvent) => {
                    const db = (<IDBRequest> result.target).result;
                    var objectStore = db.transaction(storeName).objectStore(storeName);
                    objectStore.openCursor().onsuccess = (event: any) => {
                        var cursor = event.target.result;
                        if (cursor) {
                            resultList.push(cursor.value);
                            cursor.continue();
                        } else {
                            db.close();
                            resolve(resultList);
                        }
                };
            };
        });
    }

    async findByKey(key: any): Promise<T> {
        const storeName = this.storeName;
        const resultObj = await new Promise<T>((resolve, reject) => {
            const openReq = this.indexedDb.open(this.dbName);
            openReq.onsuccess = (result: any) => {
                const db = result.target.result;
                var objectStore = db.transaction(storeName).objectStore(storeName);
                var request = objectStore.get(key);
                request.onsuccess = (event: any) => {
                  resolve((<T> request.result));
                };
                db.close();
            };
        });
        return resultObj;
    }

    async put(data: T) {
        const storeName = this.storeName;
        await new Promise(async (resolve, reject) => {
            const version = await RUtil.dbVersion(this.indexedDb, this.dbName);
            const openReq = this.indexedDb.open(this.dbName, version + 1);
            openReq.onupgradeneeded  = (event: any) => {
                        const planeObject = JSON.parse(JSON.stringify(data));
                        const db = event.target.result;
                        const tx = event.target.transaction;
                        const objectStore = tx.objectStore(storeName);
                        const req = objectStore.put(planeObject);
                        tx.oncomplete = (ev: any) => {
                            db.close();
                            resolve();
                        };
                        req.onerror = (ev: any) => {
                            db.close();
                            reject();
                        };
            };
        });
    }

   async deleteByKey(key: any) {
        const storeName = this.storeName;
        await new Promise( async (resolve, reject) => {
            const version = await RUtil.dbVersion(this.indexedDb, this.dbName);
            const openReq = window.indexedDB.open(this.dbName, version + 1);
            openReq.onupgradeneeded  = (result: any) => {
                    const db = result.target.result;
                    const tx = result.target.transaction;
                    var objectStore = tx.objectStore(storeName);
                    var request = objectStore.delete(key);
                    request.onerror = function(event: any) {
                        reject();
                    };
                    request.onsuccess = function(event: any) {
                        resolve(request);
                    };
                    db.close();
                };
            });
    }
}


