import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { GoodsCounter } from '../../shelf/models';
import Typography from 'material-ui/Typography';
import Avater from 'material-ui/Avatar';

const styles: StyleRules<'title' | 'root'|'content'|'bought'|'request'> = {
    root: {
        width: '100%',
    },
    content: {
        display: 'flex'
    },
    bought: {
        borderRadius: 10,
        padding: 5,
        boxSizing: 'border-box',
        border: '2px solid #FFFFFF',
        display: 'flex',
        width: '50%',
        flexWrap: 'wrap',
    },
    request: {
        borderRadius: 10,
        padding: 5,
        boxSizing: 'border-box',
        border: '2px solid #FFFFFF',
        display: 'flex',
        width: '50%',
        flexWrap: 'wrap',
    },
    title: {
        width: '100%',
    }
};

type ClassNames = keyof typeof styles;

interface BasketListProps {
    boughtCounterList: GoodsCounter[];
    requestCounterList: GoodsCounter[];
    chatContentCss: string;
}

const aggregateCheck  = (requestCounterList: GoodsCounter[], boughtCounterList: GoodsCounter[]) => {
    const checkedGoodsList = [];
    for (const req of requestCounterList) {
        let checkedGoods = {
            id: req.id,
            st: 'not_enough'
        };
        for (const bou of boughtCounterList) {
            if (bou.id === req.id ) {
                if (bou.count === req.count) {
                    checkedGoods.st = 'just';
                } else if (bou.count > req.count) {
                    checkedGoods.st = 'toomany';
                }
            }
        }
        checkedGoodsList.push(checkedGoods);
    }

    // 一つでも足りなければ
    const notEnough = checkedGoodsList.find((g) => g.st === 'not_enough');
    if (notEnough) {
        return 'not_enough';
    }
    const toomany = checkedGoodsList.find((g) => g.st === 'toomany');
    if (toomany || boughtCounterList.length > requestCounterList.length) {
        return 'toomany';
    }
    return 'just';
};

const CheckAnswer = withStyles(styles)<BasketListProps>(
    (props: BasketListProps & WithStyles<ClassNames>) => {
        const {classes, chatContentCss, requestCounterList, boughtCounterList} = props;
        const c = aggregateCheck(requestCounterList, boughtCounterList);
        return (
            <div className={chatContentCss}>
                <Typography>
                かってきて　くれて　ありがとう！
                </Typography>
                <div className={classes.content}>
                    <div className={classes.request}>
                        <div className={classes.title}>
                            たのんだ もの
                        </div>
                        {requestCounterList.map((rg) => {
                            return (
                                <div key={rg.id}>
                                <Avater src={rg.img} />
                                ×{rg.count}
                                </div>
                                 );
                        })}
                    </div>
                    <div className={classes.bought}>
                            <div className={classes.title}>
                                かってきた もの
                            </div>
                            {
                            boughtCounterList.map((rg) => {
                                return (
                                <div key={rg.id}>
                                <Avater src={rg.img} />
                                ×{rg.count}
                                </div>);
                                })  
                            }
                    </div>
                </div>
                <Typography>
                {c === 'just' && 'ぜんぶ そろっているね すばらしい！ (ﾟ∇^d)'}
                {c === 'not_enough' && 'なんか たりないな～ (・ω・｀)'}
                {c === 'toomany' && 'ちょっと おおいなー ∑(ﾟωﾟﾉ)ﾉ'}
                </Typography>
            </div>);
    }
);

export default CheckAnswer;


