import { handleActions, Action } from 'redux-actions';
import { MenuState } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: MenuState = <MenuState> {
    openMenuDrawer: false,
    showMenuButton: true
};

export default handleActions<MenuState, boolean>(
    {
        [AT.TOGGLE_MENU_DRAWER]: (state: MenuState, action: Action<boolean>): MenuState => {
            if (action.payload === undefined) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.openMenuDrawer = action.payload;
            return newState;
        },
        [AT.TOGGLE_MENU_BUTTON]: (state: MenuState, action: Action<boolean>): MenuState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.showMenuButton = action.payload;
            return newState;
        }
    }, 
    initialState
);


