import { handleActions, Action } from 'redux-actions';
import { DialogState, ToggleThanksDialog } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: DialogState = <DialogState> {
    thanksDialog: {open: false, change: 0, closeHandle: undefined},
};

export default handleActions<DialogState, {}>(
    {
        [AT.TOGGLE_THANKS_DIALOG]: (state: DialogState, action: Action<ToggleThanksDialog>): DialogState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const dialog = Object.assign({}, state.thanksDialog);
            dialog.open = action.payload.show;
            dialog.change = action.payload.change;
            dialog.closeHandle = action.payload.closeHandle;
            newState.thanksDialog = dialog;
            return newState;
        }
    }, 
    initialState
);


