import { IrootState } from '../../reducers/Root';
import { Dispatch } from 'react-redux';
import { connect } from 'react-redux';
import ReceiptAmount from './ReceiptAmount';
import { ReceiptAmountState } from './models';
import { toggleThanksDialog } from '../dialogs/actions';
import { closeRecivedMoneyDilaog } from './actions';
import { MessageState } from '../chat/models';
import { toggleChatDialog, changeMessageState } from '../chat/actions';

export interface Idispatcher {
    billProcess(change: number, handle: () => void): void;
    closeDialog(): void;
    prepareCheckThAnswerDialog(): void;
}

class ActionDispatcher  {

    private dispatch: Dispatch<IrootState>;
    private state: ReceiptAmountState;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    set State(st: ReceiptAmountState) {
        this.state = st;
    }

    get State(): ReceiptAmountState {
        return this.state;
    }

    createDispatcher(): Idispatcher {
        const self = this;
        return {
            billProcess: (change: number, handle: () => void) => {
                const dialogCloseHandle = () => {
                    handle();
                };
                self.dispatch(toggleThanksDialog(true, change, dialogCloseHandle));
            },
            closeDialog: () => {
                self.dispatch(closeRecivedMoneyDilaog());
            },
            prepareCheckThAnswerDialog: () => {
                self.dispatch(toggleChatDialog(true));
                self.dispatch(changeMessageState(MessageState.CheckRequest));
            }
        };
    }
}

const dispatcher = new ActionDispatcher();
export default connect((st: IrootState) => {
                            dispatcher.State = st.Amount; 
                            return { ...st.Shelf, ...st.Amount}; 
                        }, 
                       (dispatch: Dispatch<IrootState>) => { 
                            dispatcher.DispatchFunc = dispatch;
                            return dispatcher.createDispatcher() as Idispatcher; })
                        (ReceiptAmount);


