import { IrootState } from '../../../reducers/Root';
import { Dispatch } from 'react-redux';
import { connect } from 'react-redux';
import EditTable from './EditTable';
import { ShelfState } from '../../shelf/models';
import { Customer } from '../../customer/models';
import CustomerStore from '../../customer/models';

export interface Idispatcher {
    deleteCustomer(goods: Customer): void;
    changeName(customer: Customer, name: string): void;
    changeImage(customer: Customer, newImg: string): void;
    updateCustomer(customer: Customer): Promise<void>;
}

class ActionDispatcher  {

    private dispatch: Dispatch<IrootState>;
    private state: ShelfState;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    set State(st: ShelfState) {
        this.state = st;
    }

    get State(): ShelfState {
        return this.state;
    }
    createDispatcher(): Idispatcher {
        return {
                    deleteCustomer: async (customer: Customer) => {
                        await CustomerStore.delete(customer);
                    },
                    changeName: async (customer: Customer, name: string) => {
                        if (customer.name !== name) {
                            const newCustomer = Object.assign({}, customer);
                            newCustomer.name = name;
                            await CustomerStore.put(newCustomer);
                        }
                    },
                    changeImage: async (customer: Customer, img: string) => {
                        if (customer.img !== img) {
                            const newCustomer = Object.assign({}, customer);
                            newCustomer.img = img;
                            await CustomerStore.put(newCustomer);
                        }
                    },
                    updateCustomer: async (customer: Customer) => {
                        await CustomerStore.put(customer);
                    }
               };
    }
}

const dispatcher = new ActionDispatcher();
export default connect((st: IrootState) => {
                            return ; }, 
                       (dispatch: Dispatch<IrootState>) => { 
                        dispatcher.DispatchFunc = dispatch;
                        return dispatcher.createDispatcher() as Idispatcher; })
                        (EditTable);


