import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import Menu from '../container/menu/Index';
import green from 'material-ui/colors/green';
import Chat from '../container/chat/Index';

const styles: StyleRules< 'top' | 'body' | 'container'> = {
    top: {
        width: '100%',
        display: 'flex',
        alignItems: 'stretch',
        alignContent: 'stretch'
    },
    body: {
        boxSizing: 'border-box',
        backgroundColor: green[500],
        width: '100%',
        paddingTop: 60,
    },
    container: {
        width: '100%',
        height: '100%',
    }
};

type ClassNames = keyof typeof styles;

interface ReactChatProps {
}

interface ComponentState {
    height: number;
}

class ReactChatComponent extends React.Component<ReactChatProps & WithStyles<ClassNames>, ComponentState> {

    private resizeHandler: ( ) => void;

    constructor(props: any) {
        super(props);
        this.state = {
            height: window.innerHeight
        };
        this.resizeHandler = () => {
            this.setState({
               height: window.innerHeight 
            });
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    render() {
        const {classes} = this.props;
        const {height} = this.state;
        return (
        <div className={classes.top} >
            <AppBar position="fixed">
                <Toolbar>
                    <Menu />
                </Toolbar>
            </AppBar>
            <div
                className={classes.body}
                style={{
                    height
                }}
            >
                <div className={classes.container}>
                    <Chat />
                </div>
            </div>
        </div>
        );
    }
}

export default withStyles<{} & ClassNames>(styles)<ReactChatProps>(ReactChatComponent);


