import { createAction } from 'redux-actions';
import { Goods } from '../../goods/model';
import * as AT from '../../../constants/ActionTypes';

export const updateGoods = createAction<Goods, Goods> (
  AT.UPDATE_GOODS,
  (goods: Goods) => ( goods )
);



