import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { Idispatcher } from './Index';
import Table, { TableBody, TableCell, TableHead, TableRow } from 'material-ui/Table';
import { ShelfState } from '../../shelf/models';
import { RouteComponentProps, withRouter } from 'react-router';
import Avatar from 'material-ui/Avatar';
import IconButton from 'material-ui/IconButton';
import BackIcon from 'material-ui-icons/ArrowBack';
import AddIcon from 'material-ui-icons/Add';
import Input  from 'material-ui/Input';
import DeleteIcon from 'material-ui-icons/Delete';
import AddPhotoIcon from 'material-ui-icons/AddAPhoto';
import Typography from 'material-ui/Typography';
import dc from '../../../constants/DomainConstants';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import Button from 'material-ui/Button';
import { Goods } from '../../goods/model';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import loadImage from '../../../util/loadImg';
import ButtonBase from 'material-ui/ButtonBase';
import { ClassNames, styles, cellStyle, colStyle } from '../styles/styles';

class GoodsEditTable extends React.Component<Idispatcher &
                                            ShelfState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>,
                                            {
                                             openDeleteDialog: boolean,
                                             selectedGoods: Goods|null
                                            }> {
    constructor(props: any) {
      super(props);
      this.state = {
        openDeleteDialog: false,
        selectedGoods: null
      };
    }

    handleBack = () => {
      this.props.history.goBack();
    }

    linkToAddGoods = () => {
      this.props.history.push(dc.addGoods);
    }

    openDeleteDialog = (goods: Goods) => {
      this.setState({
        selectedGoods: goods,
        openDeleteDialog: true
      });
    }

    doDelete = () => {
      if (this.state.selectedGoods) {
        this.props.deleteGoods(this.state.selectedGoods);
      }
      this.setState({
        selectedGoods: null,
        openDeleteDialog: false
      });
    }

    cancelDelete = () => {
      this.setState({
        selectedGoods: null,
        openDeleteDialog: false
      });
    }

    changePrice = async (targetGood: Goods, priceStr: string) => {
      await this.props.changePrice(targetGood, Number(priceStr));
    }

    changeName = async (targetGood: Goods, priceStr: string) => {
      await this.props.changeName(targetGood, priceStr);
    }

    updateGoods = async (goods: Goods) => {
      await this.props.updateGoods(goods);
    }

    changeImg = async (goods: Goods) => {
      const result = await loadImage();
      await this.props.changeImage(goods, result);
    }

    render() {
        const {GoodsList, classes} = this.props;
        return (
          <div className={classes.top}>
          <AppBar position="fixed">
            <Toolbar className={classes.navi}>
              <IconButton onClick={this.handleBack}>
                <BackIcon style={cellStyle}/>
              </IconButton>
              <ButtonBase 
                className={classes.addBut}
                onClick={this.linkToAddGoods}
              >
                <AddIcon style={cellStyle}/>
                <Typography style={cellStyle}>ついか</Typography>
              </ButtonBase>
            </Toolbar>
          </AppBar>
            <div className={classes.table}>
              <Table >
              <TableHead>
                <TableRow>
                  <TableCell>
                    <Typography style={colStyle}>
                    イメージ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                      なまえ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    ねだん
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    さくじょ
                    </Typography>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {GoodsList.map(n => {
                  return (
                    <TableRow key={n.id}>
                      <TableCell className={classes.avaterCell}>
                          <Button
                            size="small" 
                            className={classes.avaterBut}
                            onClick={ () => {
                              this.changeImg(n);
                            }}
                          >
                            {(() => {
                              if (n.img === '' || n.img === undefined) {
                                return (<Avatar><AddPhotoIcon /></Avatar>);
                              } else {
                                return <Avatar src={n.img}/>;
                              }
                            })()}
                          </Button>
                       </TableCell>
                      <TableCell >
                          <Input
                            className={classes.inputValue}
                            value={n.displayName}
                            onChange={(evt: React.ChangeEvent<HTMLInputElement>) => {
                              this.changeName(n, evt.target.value);
                            }}
                            onBlur={async (evt: any) => {
                              await this.updateGoods(n);
                            }}                           
                          />
                      </TableCell>
                      <TableCell >
                        <Input 
                          value={n.price} 
                          type="number" 
                          className={classes.inputValue}
                          onChange={(evt: React.ChangeEvent<HTMLInputElement>) => {
                            this.changePrice(n, evt.target.value);
                          }}
                          onBlur={(evt: any) => {
                            this.updateGoods(n);
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <IconButton 
                          style={cellStyle} 
                          onClick={this.openDeleteDialog.bind(this, n)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
          </Table>
          </div>
          <Dialog open={this.state.openDeleteDialog}>
            <DialogTitle>ちゅうい</DialogTitle>
            <DialogContent>
              <DialogContentText>
                しょうひん を けします <br/>
                いいですか？
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.cancelDelete} color="default">
                ×
              </Button>
              <Button onClick={this.doDelete} color="primary">
                〇
              </Button>
            </DialogActions>
          </Dialog>
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            ShelfState & RouteComponentProps<any>>(GoodsEditTable);

export default withRouter(StyledLiveContainer);



