import { handleActions, Action } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';
import { ChatState, MessageState } from './models';

const initialState: ChatState = <ChatState> {
    openChatDialog: false,
    messageState: MessageState.request
};

export default handleActions<ChatState,  MessageState | boolean>(
    {
        [AT.CHANGE_MESSAGE_STATE]: (state: ChatState, action: Action<MessageState>): ChatState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.messageState = action.payload;
            return newState;
        },
        [AT.TOGGLE_CHAT_DIALOG]: (state: ChatState, action: Action<boolean>): ChatState => {
            if (action.payload === undefined) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.openChatDialog = action.payload;
            return newState;
        },
    }, 
    initialState
);


