import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import ListItem from 'material-ui/List/ListItem';
import NextIcon from 'material-ui-icons/NavigateNext';
import Button from 'material-ui/Button/Button';
import Divider from 'material-ui/Divider/Divider';
import Typography from 'material-ui/Typography/Typography';
import grey from 'material-ui/colors/grey';
import green from 'material-ui/colors/green';
import {  RouteComponentProps, withRouter } from 'react-router';
import dc from '../../constants/DomainConstants';
import BasketList from '../../components/common/BasketList';
import ListItemText from 'material-ui/List/ListItemText';
import { ShelfState } from '../shelf/models';

const styles: StyleRules<  'top' | 'basketFooter' | 'basketActions' | 'basketList' > = {
    top: {
        width: '100%',
    },
    basketFooter: {
        marginTop: 10,
        width: '100%',
        display: 'flex',
        justifyContent: 'flex-end',
    },
    basketActions: {
        marginRight: 20,
    },
    basketList: {
        backgroundColor: green[600],
        width: '100%',
        height: window.innerHeight * 0.65,
        overflow: 'auto'
    },
};

type ClassNames = keyof typeof styles;

interface BasketProps {
}

interface BasketComponentState {
    basketHeight: number;
}

class BasketContainer extends React.Component<Idispatcher &
                                            BasketProps &
                                            ShelfState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>, 
                                            BasketComponentState> {
    constructor(props: any) {
        super(props);
        this.state = {
            basketHeight: Math.floor(window.innerHeight * 0.65)
        };
    }
    
    nextStep = () => {
        this.props.history.push(dc.pay);
    }

    increment = (id: string) => {
        this.props.incrementGoods(id);
    }

    decrement = (id: string) => {
        this.props.decrementGoods(id);
    }

    resizeHandler = () => {
        this.setState({
            basketHeight: Math.floor(window.innerHeight * 0.65)
        });
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    render(): JSX.Element {
        const { classes, counterList, TotalPayment }  = this.props;
        return (
        <div className={classes.top}>
            <div className={classes.basketList} style={{height: this.state.basketHeight}}>
            <BasketList 
                counterList={counterList} 
                increment={this.increment} 
                decrement={this.decrement}
            />
            </div>
            <Divider />
            <div>
            <ListItem>
                <ListItemText 
                    primary={
                    <Typography type="headline" style={{ color: grey[50] }}>そうがく</Typography>}
                />
                <Typography type="headline" style={{ color: grey[50] }}>{TotalPayment}</Typography>
            </ListItem>
            </div>
            <div className={classes.basketFooter}>
                <div className={classes.basketActions}>
                <Button 
                    raised={true} 
                    color="primary" 
                    disabled={counterList.length === 0}
                    onClick={this.nextStep}
                >
                    おかいけい
                    <NextIcon />
                </Button>
                </div>
            </div>
        </div>
        );
    }
}

const StyledContainer = withRouter(withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            BasketProps & ShelfState & RouteComponentProps<any>>(BasketContainer));

export default StyledContainer;


