import { IrootState } from '../../../reducers/Root';
import { Dispatch } from 'react-redux';
import { connect } from 'react-redux';
import AddGoods from './AddGoods';
import { EditGoodsState } from '../models';
import { setImg, setPrice, setName, resetGoodsState } from '../actions';
import { Goods } from '../../goods/model';
import GoodsStore from '../../goods/model';
import { SetGoodsList } from '../../shelf/actions';
import { uuid } from '../../../util/uuid';

export interface Idispatcher {
    setName: (name: string) => void;
    setPrice: (price: number) => void;
    setImg: (img: string) => void;
    create: () => Promise<boolean>;
    clearGoodsState: () => void;
}

class ActionDispatcher  {

    private dispatch: Dispatch<IrootState>;
    private state: EditGoodsState;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    set State(st: EditGoodsState) {
        this.state = st;
    }

    get State(): EditGoodsState {
        return this.state;
    }

    async create(): Promise<boolean> {
        const goods = {
            id: uuid(),
            displayName: this.state.name,
            img: this.state.img,
            price: this.state.price
        } as Goods;
        await GoodsStore.newGoods(goods);
        this.dispatch(SetGoodsList(await GoodsStore.all()));
        this.dispatch(resetGoodsState());
        return true;
    }

    createDispatcher(): Idispatcher {
        const self = this;
        return {
            setName: (name: string) => {
                self.dispatch(setName(name));
            },
            setPrice: (price: number) => {
                self.dispatch(setPrice(price));
            },
            setImg: (img: string) => {
                self.dispatch(setImg(img));
            },
            clearGoodsState: () => {
                self.dispatch(resetGoodsState());
            },
            create: async () => {
                self.create();
                return false;
            },
        };
    }
}

const dispatcher = new ActionDispatcher();
export default connect((st: IrootState) => {
                            dispatcher.State = st.EditGoods; 
                            return dispatcher.State; }, 
                       (dispatch: Dispatch<IrootState>) => { 
                        dispatcher.DispatchFunc = dispatch;
                        return dispatcher.createDispatcher() as Idispatcher; })
                        (AddGoods);


