import { createAction } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';

export const setName = createAction<string, string>(
  AT.SET_GOODS_NAME,
  (name: string) => ( name )
);
export const setImg = createAction<string, string>(
  AT.SET_GOODS_IMG,
  (img: string) => ( img )
);
export const setPrice = createAction<number, number>(
  AT.SET_GOODS_PRICE,
  (price: number) => ( price )
);
export const resetGoodsState = createAction(
  AT.RESET_EDIT_GOODS_STATE,
);



