import { handleActions, Action } from 'redux-actions';
import { OCRState, OCRrecognizing, } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: OCRState = <OCRState> {
    OcrReady: false,
    Recognizing: false
};

export default handleActions<OCRState, OCRrecognizing>(
    {
        [AT.OCR_READY]: (state: OCRState): OCRState => {
            const newState = Object.assign({}, state);
            newState.OcrReady = true;
            return newState;
        },
        [AT.OCR_RECOGNIZING]: (state: OCRState, action: Action<OCRrecognizing>): OCRState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.Recognizing = action.payload.recognizing;
            return newState;
        }
    }, 
    initialState
);


