import { handleActions, Action } from 'redux-actions';
import { ReceiptAmountState, ReceiptStrings, AmountConstants, AmountType, CoinCounter } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: ReceiptAmountState = <ReceiptAmountState> {
    ReceiptAmount: 0,
    CoinCounters: [],
    openRecivedMoneyDialog: false
};

const incrementCoin = (counterList: CoinCounter[], type: AmountType) => {
    let counter = counterList.find((cl) => (cl.type === type));
    if (counter) {
        counter.count++;
    } else {
        counterList.push({
            type: type,
            count: 1
        });
    }
};

const decrementCoin = (counterList: CoinCounter[], type: AmountType) => {
    let counter = counterList.find((cl) => (cl.type === type));
    if (counter && counter.count > 0) {
        counter.count--;
        if (counter.count <= 0) {
            counterList.splice(counterList.indexOf(counter), 1);
        }
    }
};

const calcAmount = (counterList: CoinCounter[]): number => {
    let sum = 0;
    for (let counter of counterList) {
        sum += counter.type * counter.count;
    }
    return sum;
};

export default handleActions<ReceiptAmountState, ReceiptStrings | void | AmountType>(
    {
        [AT.RECIVED_MONEY]: (state: ReceiptAmountState, action: Action<ReceiptStrings>): ReceiptAmountState => {
            if (!action.payload) {
                return state;
            }

            const newState = Object.assign({}, state);
            newState.openRecivedMoneyDialog = true;
            const strList  = action.payload.strList;
            if (strList.length === 0) {
                return newState;
            }

            const constants = strList.map((str) => {
                const num = Number(str.replace('_', ''));
                const constant = AmountConstants.filter((ac) => {
                    return ac.value === num;
                });
                return constant[0]; 
            }).filter((c) => (c !== undefined));

            if (constants.length === 0) {
                return newState;
            }
            newState.ReceiptAmount = constants.reduce((p, c) => 
                                ({displayName: 'none', img: '',
                                  value: p.value + c.value, type: AmountType.Yen1})).value;
            const counterList: CoinCounter[] = [];
            for (let am of constants) {
                let counter = counterList.filter((cl) => (cl.type === am.type))[0];
                if (counter) {
                    counter.count++;
                } else {
                    counterList.push({
                        type: am.type,
                        count: 1
                    });
                }
            }
            newState.CoinCounters = counterList;
            return newState;
        },
        [AT.RESET_STATE]: (state: ReceiptAmountState): ReceiptAmountState => {
            const newState = Object.assign({}, state);
            newState.CoinCounters = [];
            newState.ReceiptAmount = 0;
            newState.openRecivedMoneyDialog = false;
            return newState;
        },
        [AT.CLOSE_RECEIVED_MONEY_DIALOG]: (state: ReceiptAmountState): ReceiptAmountState => {
            const newState = Object.assign({}, state);
            newState.openRecivedMoneyDialog = false;
            return newState;
        },
        [AT.INCREMENT_COIN]: (state: ReceiptAmountState, action: Action<AmountType>): ReceiptAmountState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const newList: CoinCounter[] = Array.apply(null, newState.CoinCounters);
            incrementCoin(newList, action.payload);
            newState.CoinCounters = newList;
            newState.ReceiptAmount = calcAmount(newList); 
            return newState;
        },
        [AT.DECREMENT_COIN]: (state: ReceiptAmountState, action: Action<AmountType>): ReceiptAmountState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const newList: CoinCounter[] = Array.apply(null, newState.CoinCounters);
            decrementCoin(newList, action.payload);
            newState.CoinCounters = newList;
            newState.ReceiptAmount = calcAmount(newList); 
            return newState;
        }
    }, 
    initialState
);



