import { IrootState } from '../../../reducers/Root';
import { Dispatch } from 'react-redux';
import { connect } from 'react-redux';
import EditTable from './EditTable';
import { ShelfState } from '../../shelf/models';
import GoodsStore, { Goods } from '../../goods/model';
import { SetGoodsList } from '../../shelf/actions';
import { updateGoods } from './actions';

export interface Idispatcher {
    deleteGoods(goods: Goods): void;
    changePrice(goods: Goods, newPrice: number): void;
    changeName(goods: Goods, newName: string): void;
    changeImage(goods: Goods, newName: string): void;
    updateGoods(goods: Goods): Promise<void>;
}

class ActionDispatcher  {

    private dispatch: Dispatch<IrootState>;
    private state: ShelfState;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    set State(st: ShelfState) {
        this.state = st;
    }

    get State(): ShelfState {
        return this.state;
    }

    createDispatcher(): Idispatcher {
        const self = this;
        return {
            deleteGoods: async (goods: Goods) => {
                await GoodsStore.deleteGoods(goods);
                const goodsList = await GoodsStore.all();
                self.dispatch(SetGoodsList(goodsList));
            },
            changePrice: async (goods: Goods, price: number) => {
                if (goods.price !== price) {
                    const newGoods = Object.assign({}, goods);
                    newGoods.price = price;
                    self.dispatch(updateGoods(newGoods));
                }
            },
            changeName: async (goods: Goods, name: string) => {
                if (goods.displayName !== name) {
                    const newGoods = Object.assign({}, goods);
                    newGoods.displayName = name;
                    self.dispatch(updateGoods(newGoods));
                }
            },
            changeImage: async (goods: Goods, img: string) => {
                if (goods.displayName !== img) {
                    const newGoods = Object.assign({}, goods);
                    newGoods.img = img;
                    self.dispatch(updateGoods(newGoods));
                }
            },
            updateGoods: async (goods: Goods) => {
                await GoodsStore.putGoods(goods);
            }
        };
    }
}

const dispatcher = new ActionDispatcher();
export default connect((st: IrootState) => {
                            dispatcher.State = st.Shelf; 
                            return dispatcher.State; }, 
                       (dispatch: Dispatch<IrootState>) => { 
                        dispatcher.DispatchFunc = dispatch;
                        return dispatcher.createDispatcher() as Idispatcher; })
                        (EditTable);


