import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { RouteComponentProps, withRouter } from 'react-router';
import green from 'material-ui/colors/green';
import Button from 'material-ui/Button';
import { EditGoodsState } from '../models';
import GoodsEditorComponent from '../../../components/common/GoodsEditorComponent';

const styles: StyleRules<'actions' | 'top'> = {
  actions: {
    width: '100%',
    display: 'flex',
    justifyContent: 'flex-end',
    boxSizing: 'border-box',
  },
  top: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: green[700],
    width: '100%',
    minHeight: window.innerHeight
  }
};

type ClassNames = keyof typeof styles;

class AddGoods extends React.Component<Idispatcher &
                                            EditGoodsState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>,
                                            {}> {

    createGoods = async () => {
      await this.props.create();
      this.props.history.goBack();
    }

    back = () => {
      this.props.clearGoodsState();
      this.props.history.goBack();
    }

    render() {
        const {classes, name, price, img, setImg, setName, setPrice} = this.props;
        return (
          <div className={classes.top}>
                  <GoodsEditorComponent
                    img={img}
                    name={name}
                    price={price}
                    handleSetImg={setImg}
                    handleSetName={setName}
                    handleSetPrice={setPrice}
                    editorActions={(
                      <div className={classes.actions}>
                        <Button onClick={this.back}>
                          キャンセル
                        </Button>
                        <Button color="primary" raised={true} onClick={this.createGoods}>
                          さくせい
                        </Button>
                      </div>
                    )}
                  />
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            EditGoodsState & RouteComponentProps<any>>(AddGoods);

export default withRouter(StyledLiveContainer);



