import { handleActions, Action } from 'redux-actions';
import { LiveState, Media, DeviceIds, DeviceIndex } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: LiveState = <LiveState> {
    deviceIds: [],
    selectedDeviceIdIndex: 0,
    mediaStream: null,
};

export default handleActions<LiveState, Media & DeviceIds & DeviceIndex>(
    {
        [AT.SET_MEDIA_STREAM]: (state: LiveState, action: Action<Media>): LiveState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.mediaStream = action.payload.stream;
            return newState;
        },
        [AT.SET_MEDIA_DEVICE_IDS]: (state: LiveState, action: Action<DeviceIds>): LiveState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.deviceIds = action.payload.deviceIds;
            return newState;
        },
        [AT.SET_DEVICE_INDEX]: (state: LiveState, action: Action<DeviceIndex>): LiveState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.selectedDeviceIdIndex = action.payload.deviceIndex;
            return newState;
        },
    }, 
    initialState
);


