import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import MoneyInput from '../container/moneyInput/Index';
import ReceiptAmount from '../container/amount/Index';
import purple from 'material-ui/colors/purple';
import green from 'material-ui/colors/green';

const styles: StyleRules<'root'| 'live' | 'amount'> = {
    root: {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
        alignContent: 'stretch'
    },
    live: {
        width: '100%',
        backgroundColor: purple[800],
        overflow: 'scroll'
    },
    amount: {
        width: '100%',
        backgroundColor: green[800],
    }
};

type ClassNames = keyof typeof styles;

interface PaymentComponentProps {
}

interface ComponentState {
    height: number;
}

class PaymentComponent extends React.Component<PaymentComponentProps & WithStyles<ClassNames>, 
    ComponentState> {
    private resizeHandler: () => void;

    constructor(props: any) {
        super(props);
        this.state = {
            height: window.innerHeight,
        };
        this.resizeHandler = () => {
            this.setState({
               height: window.innerHeight 
            });
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    render() {
        const {classes} = this.props;
        const { height} = this.state;
        const ratio = 0.65;
        const liveHeight = height * ratio;
        const amHeight = height * (1 - ratio);
        return (
        <div className={classes.root}>
                <div 
                    className={classes.live}
                    style={{height: liveHeight}}
                >
                    <MoneyInput />
                </div>
                <div 
                    className={classes.amount}
                    style={{height: amHeight}}
                >
                    <ReceiptAmount />
                </div>
        </div>
        );
    }
}

export default withStyles<{} & ClassNames>(styles)<PaymentComponentProps>(PaymentComponent);


