import { goodslist } from './constants';
import { key, index, scheme } from '../../renga/RStoreScheme';
import Renga from '../../renga/Renga';
import { RObjectStore } from '../../renga/RObjectStore';

@scheme('Goods')
export class Goods {
    @key
    id: string;
    @index
    displayName: string;
    @index
    price: number;
    @index
    img: string;
}

const dbName = 'learnrezi';

class GoodsStore {

    private renga: Renga;
    private goodsObjectStore: RObjectStore<Goods>;

    constructor() {
        this.renga = new Renga(dbName);
    }

    async fetchDbData(completed: () => void) {
        const created = await this.renga.createObjectStoreIfNotExist(new Goods());
        this.goodsObjectStore = await this.renga.objectStore<Goods>(Goods);
        if (created) {
            for (const g of goodslist) {
                await this.goodsObjectStore.put(g);
            }
        }
        completed();
    }

    async all() {
        return await this.goodsObjectStore.all();
    }

    async newGoods(goods: Goods) {
        return await this.goodsObjectStore.put(goods);
    }

    async deleteGoods(goods: Goods) {
        await this.goodsObjectStore.deleteByKey(goods.id);
    }

    async putGoods(goods: Goods) {
        await this.goodsObjectStore.put(goods);
    }
}

export default new GoodsStore();


