
abstract class RengaError implements Error {
    public name = 'RengaError';
  
    constructor(public message: string) {
    }

    abstract getErrorName(): string;
  
    toString() {
      return this.name + ': ' + this.message;
    }
}

export class DbError extends RengaError {
    getErrorName(): string {
        return 'DB Error';
    }
}



