import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { ReceiptAmountState } from './models';
import grey from 'material-ui/colors/grey';
import Typography from 'material-ui/Typography/Typography';
import Divider from 'material-ui/Divider/Divider';
import green from 'material-ui/colors/green';
import pink from 'material-ui/colors/pink';
import Button from 'material-ui/Button/Button';
import { RouteComponentProps, withRouter } from 'react-router';
 import dc from '../../constants/DomainConstants';
import BasketList from '../../components/common/BasketList';
import ReceivedDialog, { DisplayType } from './ReceiptAmountDialog';
import { ShelfState } from '../shelf/models';

const styles: StyleRules<'footerItem' |  'top' | 'footer' | 'footerAction' | 'basketList'> = {
    top: {
        width: '100%',
        display: 'flex',
        height: '100%',
    },
    basketList: {
        width: '40%',
        height: '100%',
        backgroundColor: green[600],
        overflow: 'auto'
    },
    footer: {
        width: '60%',
        lineHeight: '0.5em'
    },
    footerItem: {
        marginLeft: 20,
        marginRight: 15,
        display: 'flex',
        justifyContent: 'space-between'
    },
    footerAction: {
        marginRight: 10,
        marginTop: 10,
        display: 'flex',
        justifyContent: 'flex-end',
    }
};

type ClassNames = keyof typeof styles;

interface ReceiptAmountProps {
}

class ReceiptAmountContainer extends React.Component<Idispatcher &
                                            ReceiptAmountProps &
                                            ReceiptAmountState &
                                            ShelfState & 
                                            RouteComponentProps<any>&
                                            WithStyles<ClassNames>, 
                                            {}> {
    bill = () => {
        const {ReceiptAmount, TotalPayment} = this.props;
        const handleCloseThxDialog = () => {
            this.props.prepareCheckThAnswerDialog();
            this.props.history.push(dc.home);
        };
        this.props.billProcess(ReceiptAmount - TotalPayment, handleCloseThxDialog);
    }

    goBack = () => {
        this.props.history.goBack();
    }

    closeDialog = () => {
        this.props.closeDialog();
    }

    render(): JSX.Element {
        const {classes, ReceiptAmount, TotalPayment, counterList, openRecivedMoneyDialog, CoinCounters} = this.props;
        let receiptColor = green[100];
        let disable = false;
        let dialogType = DisplayType.ENOUGH;
        if (ReceiptAmount < TotalPayment) {
            receiptColor = pink[300];
            disable = true;
            dialogType = DisplayType.NOT_ENOUGH;
        } else if ( ReceiptAmount === TotalPayment) {
            dialogType = DisplayType.JUST;
        }
        return (
        <div className={classes.top}>
            <div className={classes.basketList}>
                <BasketList counterList={counterList}/>
            </div>
            <div className={classes.footer}>
            <Divider light={true}/>
                <div className={classes.footerItem}>
                <Typography type="body2" style={{ color: grey[50] }}>あずかり</Typography>
                <Typography type="body2" style={{ color: receiptColor }}>{ReceiptAmount}</Typography>
                </div>
                <div className={classes.footerItem}>
                <Typography type="body1" style={{ color: grey[50] }}>そうがく</Typography>
                <Typography type="body1" style={{ color: grey[50] }}>{TotalPayment}</Typography>
                </div>
                <div className={classes.footerItem}>
                <Typography type="body1" style={{ color: grey[50] }}>おつり</Typography>
                <Typography type="body1" style={{ color: grey[50] }}>
                    {disable ? '-' : (ReceiptAmount - TotalPayment)}
                </Typography>
                </div>
                <div className={classes.footerAction}>
                <Button 
                    onClick={this.goBack}
                >
                    もどる
                </Button>
                <Button 
                    raised={true} 
                    color="primary" 
                    onClick={this.bill}
                    disabled={disable}
                >
                    せいさん
                </Button>
                </div>
            </div>
            <ReceivedDialog 
                open={openRecivedMoneyDialog}
                handelClose={this.closeDialog}
                coinCounterList={CoinCounters}
                receipt={ReceiptAmount}
                displayType={dialogType}
            />
        </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            ReceiptAmountState & ShelfState & RouteComponentProps<any> 
                            & ReceiptAmountProps>(ReceiptAmountContainer);

export default withRouter(StyledLiveContainer);


