import { handleActions, Action } from 'redux-actions';
import { ShelfState, GoodsList, GoodsCounter,  } from './models';
import * as AT from '../../constants/ActionTypes';
import { Goods } from '../goods/model';

const initialState: ShelfState = <ShelfState> {
    GoodsList: [],
    counterList: [],
    TotalPayment: 0
};

const decrementGoods = (goodsCountList: GoodsCounter[], goods: Goods): GoodsCounter[] => {
    const counter = goodsCountList.find((r) => (r.id === goods.id));
    if (counter) {
        counter.count--;
        if (counter.count <= 0) {
            goodsCountList = goodsCountList.filter((g) => (g.id !== counter.id));
        }
    }
    return goodsCountList;
};

const incrementGoods = (goodsCountList: GoodsCounter[], goods: Goods): GoodsCounter[] => {
        const counter = goodsCountList.find((r) => (r.id === goods.id));
        if (counter) {
            counter.count++;
        } else {
            goodsCountList.push({
                count: 1,
                img: goods.img,
                displayName: goods.displayName,
                id: goods.id,
                price: goods.price
            });
        }
        return goodsCountList;
 };

 const calcSum = (counterList: GoodsCounter[]): number => {
    let sum = 0;
    for (let counter of counterList) {
        sum += (counter.price * counter.count);
    }
    return sum;
 };

export default handleActions<ShelfState, GoodsList & Goods & string>(
    {
        [AT.SET_GOODS_LIST]: (state: ShelfState, action: Action<GoodsList>): ShelfState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.GoodsList = action.payload.goodsList;
            return newState;
        },
        [AT.SELECT_GOODS]: (state: ShelfState, action: Action<Goods>): ShelfState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const newConterList = Array.apply(null, newState.counterList);
            newState.counterList = incrementGoods(newConterList, action.payload);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        },
        [AT.RESET_STATE]: (state: ShelfState): ShelfState => {
            const newState = Object.assign({}, state);
            newState.counterList = [];
            newState.TotalPayment = 0;
            return newState;
        },
        [AT.INCREASE_GOODS]: (state: ShelfState, action: Action<string>): ShelfState => {
            if (!action.payload) {
                return state;
            }
            const goodsId = action.payload;
            const newState = Object.assign({}, state);
            const newCounterList = Array.apply(null, newState.counterList);
            const goods = newState.GoodsList.find((g) => {return g.id === goodsId; });
            if (!goods) {
                return state;
            }
            newState.counterList = incrementGoods(newCounterList, goods);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        },
        [AT.DECREASE_GOODS]: (state: ShelfState, action: Action<string>): ShelfState => {
            if (!action.payload) {
                return state;
            }
            const goodsId = action.payload;
            const newState = Object.assign({}, state);
            const newCounterList = Array.apply(null, newState.counterList);
            const goods = newState.GoodsList.find((g) => {return g.id === goodsId; });
            if (!goods) {
                return state;
            }
            newState.counterList = decrementGoods(newCounterList, goods);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        },
        [AT.UPDATE_GOODS]: (state: ShelfState, action: Action<Goods>): ShelfState => {
            if (!action.payload) {
                return state;
            }
            const targetGoods = action.payload;
            const newState = Object.assign({}, state);
            const goodsList = Array.apply(null, newState.GoodsList);
            const index = newState.GoodsList.findIndex((g) => {return g.id === targetGoods.id; });
            if (index < 0) {
                return state;
            }
            goodsList[index] = targetGoods;
            newState.GoodsList = goodsList;
            return newState;
        }
    }, 
    initialState
);


